import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom";
import "../styles/global.css";
import style from "../styles/login.module.css";
import { hot } from "react-hot-loader/root";
import APIService from "../servers/APIService";

function Login() {
    useEffect(() => {}, []);

    const loginBtnClicked = (event) => {
        event.preventDefault();

        APIService.login().then((res) => {
            if (res && res.success === 1) {
                location.href = "./index.html";
            }
        });
    };
    return (
        <>
            <h1 className={style.pir_rg}>Sign In</h1>
            <p className={style.pir_rg}>Log in to your account to continue.</p>

            <form className="text-left">
                <div className="form">
                    <div id="username-field" className="field-wrapper input">
                        <label className={style.pir_rg} htmlFor="username">
                            USERNAME
                        </label>
                        <svg
                            style={{ top: 63 }}
                            xmlns="http://www.w3.org/2000/svg"
                            width="24"
                            height="24"
                            viewBox="0 0 24 24"
                            fill="none"
                            stroke="currentColor"
                            strokeWidth="2"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            className="feather feather-user"
                        >
                            <path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2"></path>
                            <circle cx="12" cy="7" r="4"></circle>
                        </svg>
                        <input
                            id="username"
                            name="username"
                            type="text"
                            className="form-control"
                            placeholder="AiBankID/Email/Tel."
                        />
                    </div>

                    <div id="password-field" className="field-wrapper input mb-2">
                        <div className="d-flex justify-content-between">
                            <label className={style.pir_rg} htmlFor="password">
                                PASSWORD
                            </label>
                            <a className={`${style.pir_rg} style.pir_rg`} href="./password.html">
                                Forgot Password?
                            </a>
                        </div>
                        <svg
                            style={{ top: 60 }}
                            xmlns="http://www.w3.org/2000/svg"
                            width="24"
                            height="24"
                            viewBox="0 0 24 24"
                            fill="none"
                            stroke="currentColor"
                            strokeWidth="2"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            className="feather feather-lock"
                        >
                            <rect x="3" y="11" width="18" height="11" rx="2" ry="2"></rect>
                            <path d="M7 11V7a5 5 0 0 1 10 0v4"></path>
                        </svg>
                        <input
                            id="password"
                            name="password"
                            type="password"
                            className="form-control"
                            placeholder="Password"
                        />
                        <svg
                            style={{ top: 60 }}
                            xmlns="http://www.w3.org/2000/svg"
                            width="24"
                            height="24"
                            viewBox="0 0 24 24"
                            fill="none"
                            stroke="currentColor"
                            strokeWidth="2"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            id="toggle-password"
                            className="feather feather-eye"
                        >
                            <path d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"></path>
                            <circle cx="12" cy="12" r="3"></circle>
                        </svg>
                    </div>
                    <div className="d-sm-flex justify-content-between">
                        <div className="field-wrapper">
                            <button
                                style={{ height: 55 }}
                                type="submit"
                                className={`btn btn-primary ${style.pir_rg}`}
                                value=""
                                onClick={loginBtnClicked}
                            >
                                Log In
                            </button>
                        </div>
                    </div>

                    <div className="division">
                        <span>OR</span>
                    </div>

                    <div className={`${style.google_btn} ${style.center}`}>
                        <i className="icon-google" />
                        <span>Google</span>
                    </div>

                    <p className="signup-link">
                        Not registered ?<a href="./register.html">Create an account</a>
                    </p>
                    <div className={`${style.help_view} ${style.center}`}>
                        <i className="icon-help" />
                        <span>HELP</span>
                    </div>
                </div>
            </form>
        </>
    );
}
const WithHotReload = process.env.NODE_ENV === "production" ? Login : hot(Login);

ReactDOM.render(<WithHotReload />, document.getElementById("root"));
